<template>
    <div class="Previewer__content">
        <Landing
                :settings="settings"
                :step="step"
                v-if="type === previewType.landing"
                @changeStep="notifyHandler('changeStep', $event)"
        />
    </div>
</template>

<script>
    import {previewType} from "@/constants";
    import Landing from "@/components/Previewer/Landing/Landing";

    export default {
        name: "Previewer",
        props: {
            type: {required: true, type: Number},
            step: {required: true, type: Number},
            settings: {required: true, type: Object}
        },
        components: {
            Landing
        },
        data() {
            return {
                previewType: previewType
            }
        },
        methods: {
            notifyHandler(eventName, params) {
                this.$emit(eventName, params);
            }
        }
    }
</script>

<style src="./style.css"/>