<template>
    <StepTemplate title="Шаг 1. Выбор типа ресурса">
        <template v-slot:StepTemplate>
            <el-radio-group v-model="type">
                <el-radio class="Step1__item" :label="previewType.landing">
                    <div class="Step1__item-content">
                        <span class="Step1__title flex flex-align-center">Лендинг</span>
                        <el-tooltip class="Step1__tooltip" effect="dark" :content="tooltip.step1.landing"
                                    placement="top-start">
                            <el-button icon="el-icon-question" type="primary" size="small" plain></el-button>
                        </el-tooltip>
                    </div>
                </el-radio>
                <el-radio class="Step1__item" :label="previewType.site">
                    <div class="Step1__item-content">
                        <span class="Step1__title flex flex-align-center">Сайт</span>
                        <el-tooltip class="Step1__tooltip" effect="dark" :content="tooltip.step1.site"
                                    placement="top-start">
                            <el-button icon="el-icon-question" type="primary" size="small" plain></el-button>
                        </el-tooltip>
                    </div>
                </el-radio>
                <el-radio class="Step1__item" :label="previewType.portal">
                    <div class="Step1__item-content">
                        <span class="Step1__title flex flex-align-center">Портал</span>
                        <el-tooltip class="Step1__tooltip" effect="dark" :content="tooltip.step1.portal"
                                    placement="top-start">
                            <el-button icon="el-icon-question" type="primary" size="small" plain></el-button>
                        </el-tooltip>
                    </div>
                </el-radio>
                <el-radio class="Step1__item" :label="previewType.application">
                    <div class="Step1__item-content">
                        <span class="Step1__title flex flex-align-center">Веб-приложение</span>
                        <el-tooltip class="Step1__tooltip" effect="dark" :content="tooltip.step1.application"
                                    placement="top-start">
                            <el-button icon="el-icon-question" type="primary" size="small" plain></el-button>
                        </el-tooltip>
                    </div>
                </el-radio>
            </el-radio-group>
        </template>
    </StepTemplate>
</template>

<script>
    import StepTemplate from "@/components/SwitchableArea/StepTemplate/StepTemplate";
    import {previewType, price, tooltip} from "@/constants";

    export default {
        name: "Step1",
        components: {StepTemplate},
        props: {
            typePreview: {required: true, type: Number}
        },
        data() {
            return {
                previewType: previewType,
                type: this.typePreview,
                price: price.type,
                tooltip: tooltip
            }
        },
        watch: {
            type: function (val) {
                this.$emit('typeChange', val);
            }
        }
    }
</script>

<style src="./style.css"/>