<template>
    <div class="Landing__content full-width">
        <Header
                :settings="settings"
                @changeStep="notifyHandler('changeStep', $event)"
                v-if="step > 1"
        />
        <Menu
                :settings="settings"
                @changeStep="notifyHandler('changeStep', $event)"
                v-if="step > 2"
        />
    </div>
</template>

<script>
    import Header from "@/components/Previewer/components/Header/Header";
    import Menu from "@/components/Previewer/components/Menu/Menu";

    export default {
        name: "Landing",
        props: {
            settings: {required: true, type: Object},
            step: {required: true, type: Number},
        },
        components: {Header, Menu},
        methods: {
            notifyHandler(eventName, params) {
                this.$emit(eventName, params);
            }
        }
    }
</script>

<style src="./style.css"/>