<template>
    <div class="Header flex flex-align-center"
         @click="_notifyChangeStep()"
         :class="{
                'flex-justify-space-between': settings.header.align === 'size',
                'flex-justify-space-around': settings.header.align === 'around',
                'flex-justify-center': settings.header.align === 'center',
             }"
         v-if="settings.header.useHeader"
         :style="{
                background: settings.header.background,
                height: settings.header.height + (settings.header.isPercentHeight ? '%' : 'px')
             }">
        <div class="Header-logo flex flex-align-center"
             v-if="settings.header.hasLogo">
            <img
                    :class="{ 'Header-logo-img': settings.header.logo.auto}"
                    :src="settings.header.logo.url"
                    :style="settings.header.logo.auto ? {} : {
                            width: settings.header.logo.width + 'px',
                            height: settings.header.logo.height + 'px'}"
                    alt="Логотип"
                    v-if="settings.header.logo.url"
            >
        </div>
        <div class="Header-title flex flex-column"
             v-if="settings.header.hasTitle">
                <span
                        :style="{
                            color: settings.header.title.color,
                            'font-size': settings.header.title.size + 'rem',
                            'border-bottom': settings.header.title.border
                                ? '1px solid' + settings.header.title.borderColor : '0'
                        }"
                        style="cursor: default">
                    {{ settings.header.title.text }}
                </span>
            <span
                    v-if="settings.header.title.twoLine"
                    :style="{
                            color: settings.header.title.twoLineColor,
                            'font-size': settings.header.title.twoLineSize + 'rem'
                        }"
                    style="cursor: default">
                    {{ settings.header.title.twoLineText }}
                </span>
        </div>
        <div class="Header-contacts"
             v-if="settings.header.hasContacts" :style="{
                    cursor: !settings.header.contacts.clickable ? 'default' : 'pointer',
                    color: settings.header.contacts.color,
                    'font-size': settings.header.contacts.size + 'rem'
                 }">
            <div class="Header-contacts-phone"
                 v-if="settings.header.contacts.hasPhone">
                <i class="el-icon-phone" v-if="settings.header.contacts.useIcon"></i>
                <a :href="'tel:' + settings.header.contacts.phone"
                   v-if="settings.header.contacts.clickable"
                   :style="{
                            color: settings.header.contacts.color,
                            'font-size': settings.header.contacts.size + 'rem',
                            'text-decoration': settings.header.contacts.decoration && settings.header.contacts.clickable ? 'underline ' : 'none'
                       }"
                >
                    <span>{{ settings.header.contacts.phone }}</span>
                </a>
                <span v-if="!settings.header.contacts.clickable">{{ settings.header.contacts.phone }}</span>
            </div>
            <div class="Header-contacts-email"
                 v-if="settings.header.contacts.hasEmail">
                <i class="el-icon-message" v-if="settings.header.contacts.useIcon"></i>
                <a :href="'mailto:' + settings.header.contacts.email"
                   v-if="settings.header.contacts.clickable"
                   :style="{
                            color: settings.header.contacts.color,
                            'font-size': settings.header.contacts.size + 'rem',
                            'text-decoration': settings.header.contacts.decoration && settings.header.contacts.clickable ? 'underline ' : 'none'
                       }"
                   @click="_notNotify()"
                >
                    <span>{{ settings.header.contacts.email }}</span>
                </a>
                <span v-if="!settings.header.contacts.clickable">{{ settings.header.contacts.email }}</span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Header",
        props: {
            settings: {required: true, type: Object}
        },
        methods: {
            _notifyChangeStep: function () {
                //this.$emit('changeStep', 2);
            },
        }
    }
</script>

<style src="./style.css"/>