<template>
    <StepTemplate title="Шаг 3. Настройка меню">
        <template v-slot:StepTemplate>
            <div class="flex">
                <div class="Step2__itemsBlock Step3__itemsBlock">
                    <el-switch
                            class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                            v-model="settings.menu.isHorizontal"
                            active-color="#13ce66"
                            inactive-color="#5252DD"
                            active-text="Горизонтальное"
                            inactive-text="Вертикальное"
                            @change="checkSize()"
                    />
                    <el-switch
                            class="Step2__item flex flex-reverse full-width flex-align-center flex-justify-space-between"
                            v-model="settings.menu.fixed"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text="Закрепить"
                            v-if="settings.menu.isHorizontal"
                    />
                    <el-switch
                            class="Step2__item flex flex-reverse full-width flex-align-center flex-justify-space-between"
                            v-model="settings.menu.hasMinimize"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text="Сворачиваемое"
                            v-if="!settings.menu.isHorizontal"
                    />
                    <el-switch
                            class="Step2__item flex flex-reverse full-width flex-align-center flex-justify-space-between"
                            v-model="settings.menu.useIcon"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text="Иконки"
                            @change="checkTextIcon('icon')"
                    />
                    <el-switch
                            class="Step2__item flex flex-reverse full-width flex-align-center flex-justify-space-between"
                            v-model="settings.menu.useText"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text="Текст"
                            @change="checkTextIcon('text')"
                    />
                    <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                        <span>Размер меню (px)</span>
                        <el-input-number
                                v-model="settings.menu.size"
                                :min="settings.menu.isHorizontal ? 60 : 140"
                                :max="settings.menu.isHorizontal ? 100 : 300"
                                size="small"
                        />
                    </div>
                </div>
                <div class="Step2__itemsBlock Step3__itemsBlock">
                    <el-switch
                            class="Step2__item flex flex-reverse full-width flex-align-center flex-justify-space-between"
                            v-model="settings.menu.useAbout"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text="О нас"
                            @change="changeMenu()"
                    />
                    <el-switch
                            class="Step2__item flex flex-reverse full-width flex-align-center flex-justify-space-between"
                            v-model="settings.menu.useContacts"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text="Контакты"
                            @change="changeMenu()"
                    />
                    <el-switch
                            class="Step2__item flex flex-reverse full-width flex-align-center flex-justify-space-between"
                            v-model="settings.menu.useReviews"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text="Отзывы"
                            @change="changeMenu()"
                    />
                    <el-switch
                            class="Step2__item flex flex-reverse full-width flex-align-center flex-justify-space-between"
                            v-model="settings.menu.useCommand"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text="Команда"
                            @change="changeMenu()"
                    />
                    <el-switch
                            class="Step2__item flex flex-reverse full-width flex-align-center flex-justify-space-between"
                            v-model="settings.menu.useProject"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text="Проекты"
                            @change="changeMenu()"
                    />
                </div>
            </div>
        </template>
    </StepTemplate>
</template>

<script>
    import StepTemplate from "@/components/SwitchableArea/StepTemplate/StepTemplate";
    import {previewType} from "@/constants";

    export default {
        name: "Step3",
        components: {StepTemplate},
        props: {
            type: {required: true, type: Number},
            settings: {required: true, type: Object},
        },
        beforeMount() {
            this.changeMenu();
        },
        data() {
            return {
                previewType: previewType,
            }
        },
        methods: {
            changeMenu: function () {
                const menuItems = this.settings.menu.items;
                const newItems = [menuItems[0]];
                const menuSettings = this.settings.menu;
                const contactsItem = {
                    id: 2,
                    name: 'Контакты',
                    icon: 'el-icon-phone',
                    description: ''
                };
                const aboutItem = {
                    id: 3,
                    name: 'О нас',
                    icon: 'el-icon-question',
                    description: ''
                };
                const reviewItem = {
                    id: 4,
                    name: 'Отзывы',
                    icon: 'el-icon-star-off',
                    description: ''
                };
                const commandItem = {
                    id: 5,
                    name: 'Команда',
                    icon: 'el-icon-user',
                    description: ''
                };
                const projectItem = {
                    id: 6,
                    name: 'Проекты',
                    icon: 'el-icon-goods',
                    description: ''
                };

                if (menuSettings.useAbout) {
                    newItems.push(aboutItem);
                }
                if (menuSettings.useContacts) {
                    newItems.push(contactsItem);
                }
                if (menuSettings.useReviews) {
                    newItems.push(reviewItem);
                }
                if (menuSettings.useCommand) {
                    newItems.push(commandItem);
                }
                if (menuSettings.useProject) {
                    newItems.push(projectItem);
                }

                this.settings.menu.items = newItems;
            },
            checkSize: function () {
                const menuSettings = this.settings.menu;
                if (menuSettings.isHorizontal && menuSettings.size > 100) {
                    menuSettings.size = 60;
                }
                if (!menuSettings.isHorizontal && menuSettings.size < 140) {
                    menuSettings.size = 140;
                }
            },
            checkTextIcon: function (type) {
                const menuSettings = this.settings.menu;

                if (type === 'icon' && !menuSettings.useIcon && !menuSettings.useText) {
                    menuSettings.useText = true;
                }

                if (type === 'text' && !menuSettings.useIcon && !menuSettings.useText) {
                    menuSettings.useIcon = true;
                }
            }
        }
    }
</script>

<style src="./style.css"/>