<template>
    <div class="SwitchableArea__content flex flex-align-center flex-justify-center">
        <Step1
                v-if="step === 1"
                :typePreview="type"
                @typeChange="notifyHandler('typeChange', $event)"
        />
        <Step2
                v-if="step === 2"
                :type="type"
                :settings="settings"
                @typeChange="notifyHandler('typeChange', $event)"
        />
        <Step3
                v-if="step === 3"
                :type="type"
                :settings="settings"
                @typeChange="notifyHandler('typeChange', $event)"
        />
    </div>
</template>

<script>
    import Step1 from "@/components/SwitchableArea/Step1/Step1";
    import Step2 from "@/components/SwitchableArea/Step2/Step2";
    import Step3 from "@/components/SwitchableArea/Step3/Step3";

    export default {
        name: "SwitchableArea",
        props: {
            type: {required: true, type: Number},
            step: {required: true, type: Number},
            settings: {required: true, type: Object},
        },
        components: {Step1, Step2, Step3},
        methods: {
            notifyHandler(eventName, params) {
                this.$emit(eventName, params);
            }
        }
    }
</script>

<style src="./style.css"/>