<template>
    <div class="Header__content flex flex-justify-space-between hidden-overflow">
        <div class="Header__content-main full-width">
            <div class="Header__content-button flex flex-align-center">
                <el-button
                        type="primary"
                        icon="el-icon-arrow-left"
                        v-if="currentStep > 1"
                        @click="btnClick(btnType.prev)"
                >
                    Предыдущий шаг
                </el-button>
            </div>
            <div class="Header__content-info flex flex-align-center flex-justify-center"></div>
            <div class="Header__content-button flex flex-align-center flex-justify-end">
                <el-button
                        type="primary"
                        @click="btnClick(btnType.next)"
                        v-if="currentStep !== maxStep"
                >
                    Следующий шаг
                    <i class="el-icon-arrow-right"></i>
                </el-button>
                <el-button
                        type="primary"
                        v-if="currentStep === maxStep"
                >
                    Оформить ТЗ
                    <i class="el-icon-tickets"></i>
                </el-button>
            </div>
        </div>
        <div class="Header__content-progressBar">
            <el-progress status="success" :percentage="(currentStep / maxStep) * 100"></el-progress>
        </div>
        <div class="shadow"></div>
    </div>
</template>

<script>
    import {btnType} from "@/constants";

    export default {
        name: "Header",
        props: {
            currentStep: {required: true, type: Number},
            maxStep: {required: true, type: Number},
            cost: {required: true, type: Number},
        },
        data() {
            return {
                btnType: btnType
            }
        },
        methods: {
            btnClick(type) {
                this.$emit('changeCount', type);
            }
        }
    }
</script>

<style src="./style.css"/>