<template>
    <StepTemplate title="Шаг 2. Настройка заголовка">
        <template v-slot:StepTemplate>
            <div class="flex">
                <div class="Step2__itemsBlock">
                    <el-switch
                            class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                            v-model="settings.header.useHeader"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text="Использовать заголовок"
                            inactive-text="Без заголовка"
                            :disabled="type !== previewType.application"
                    />
                    <div v-if="settings.header.useHeader">
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>Цвет фона загаловка</span>
                            <el-color-picker
                                    v-model="settings.header.background"
                                    size="small"
                            />
                        </div>
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.isPercentHeight"
                                active-color="#13ce66"
                                inactive-color="#5252DD"
                                active-text="Высота в процентах"
                                inactive-text="Высота в пикселях"
                        />
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>Высота заголовка {{ settings.header.isPercentHeight ? '(%)' : '(px)' }}</span>
                            <el-input-number
                                    v-model="settings.header.height"
                                    :min="settings.header.isPercentHeight ? 2 : 40"
                                    :max="settings.header.isPercentHeight ? 100 : 500"
                                    size="small"
                            />
                        </div>
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>Расположение</span>
                            <el-select
                                    size="small"
                                    v-model="settings.header.align"
                            >
                                <el-option
                                        v-for="item in sizeOptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                />
                            </el-select>
                        </div>


                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.hasLogo"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text="Есть логотип"
                                inactive-text="Нет логотипа"
                        />
                        <div v-if="settings.header.hasLogo">
                            <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                                <span>Ссылка на лого</span>
                                <el-input
                                        placeholder="Вставьте ссылку"
                                        v-model="settings.header.logo.url"
                                        size="small"
                                        :clearable="true"
                                />
                            </div>
                            <el-switch
                                    class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                    v-model="settings.header.logo.auto"
                                    active-color="#13ce66"
                                    inactive-color="#ff4949"
                                    active-text="Авто размер"
                                    inactive-text="Ручной ввод"
                            />
                            <div v-if="!settings.header.logo.auto">
                                <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                                    <span>Ширина логотипа</span>
                                    <el-input-number
                                            v-model="settings.header.logo.width"
                                            :min="20"
                                            size="small"
                                    />
                                </div>
                                <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                                    <span>Высота логотипа</span>
                                    <el-input-number
                                            v-model="settings.header.logo.height"
                                            :min="20"
                                            size="small"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="Step2__itemsBlock" v-if="settings.header.useHeader">
                    <el-switch
                            class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                            v-model="settings.header.hasTitle"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text="Есть название"
                            inactive-text="Нет названия"
                    />
                    <div v-if="settings.header.hasTitle">
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>Название</span>
                            <el-input
                                    placeholder="Введите основной заголовок на сайт"
                                    v-model="settings.header.title.text"
                                    size="small"
                                    :clearable="true"
                            />
                        </div>
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>Цвет названия</span>
                            <el-color-picker
                                    v-model="settings.header.title.color"
                                    size="small"
                            />
                        </div>
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>Размер шрифта названия</span>
                            <el-input-number
                                    v-model="settings.header.title.size"
                                    :min="0.5"
                                    :max="10"
                                    :step="0.1"
                                    size="small"
                            />
                        </div>
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.title.border"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text="С подчеркиванием"
                                inactive-text="Без подчеркивания"
                        />
                        <div v-if="settings.header.title.border"
                             class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>Цвет подчеркивания</span>
                            <el-color-picker
                                    v-model="settings.header.title.borderColor"
                                    size="small"
                            />
                        </div>
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.title.twoLine"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text="Доп. строка"
                                inactive-text="Только название"
                        />
                        <div v-if="settings.header.title.twoLine">
                            <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                                <span>Название</span>
                                <el-input
                                        placeholder="Введите основной заголовок на сайт"
                                        v-model="settings.header.title.twoLineText"
                                        size="small"
                                        :clearable="true"
                                />
                            </div>
                            <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                                <span>Цвет названия</span>
                                <el-color-picker
                                        v-model="settings.header.title.twoLineColor"
                                        size="small"
                                />
                            </div>
                            <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                                <span>Размер шрифта названия</span>
                                <el-input-number
                                        v-model="settings.header.title.twoLineSize"
                                        :min="0.5"
                                        :max="10"
                                        :step="0.1"
                                        size="small"
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <div class="Step2__itemsBlock" v-if="settings.header.useHeader">
                    <el-switch
                            class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                            v-model="settings.header.hasContacts"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            active-text="С контактами"
                            inactive-text="Без контактов"
                    />
                    <div v-if="settings.header.hasContacts">
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.contacts.clickable"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text="Ссылки"
                                inactive-text="Текст"
                        />
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.contacts.useIcon"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text="С иконками"
                                inactive-text="Без иконок"
                        />
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.contacts.decoration"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text="С подчеркиванием"
                                inactive-text="Без подчеркивания"
                        />
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>Цвет контактов</span>
                            <el-color-picker
                                    v-model="settings.header.contacts.color"
                                    size="small"
                            />
                        </div>
                        <div class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>Размер шрифта контактов</span>
                            <el-input-number
                                    v-model="settings.header.contacts.size"
                                    :min="0.5"
                                    :max="10"
                                    :step="0.1"
                                    size="small"
                            />
                        </div>
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.contacts.hasPhone"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text="Есть телефон"
                                inactive-text="Нет телефона"
                        />
                        <div v-if="settings.header.contacts.hasPhone" class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>Телефон</span>
                            <el-input
                                    placeholder="Номер телефона"
                                    v-model="settings.header.contacts.phone"
                                    size="small"
                                    :clearable="true"
                            />
                        </div>
                        <el-switch
                                class="Step2__item flex full-width flex-align-center flex-justify-space-between"
                                v-model="settings.header.contacts.hasEmail"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text="Есть почта"
                                inactive-text="Нет почты"
                        />
                        <div v-if="settings.header.contacts.hasEmail" class="Step2__item flex full-width flex-align-center flex-justify-space-between">
                            <span>Почта</span>
                            <el-input
                                    placeholder="Почта"
                                    v-model="settings.header.contacts.email"
                                    size="small"
                                    :clearable="true"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </StepTemplate>
</template>

<script>
    import StepTemplate from "@/components/SwitchableArea/StepTemplate/StepTemplate";
    import {previewType} from "@/constants";

    export default {
        name: "Step2",
        props: {
            type: {required: true, type: Number},
            settings: {required: true, type: Object},
        },
        components: {StepTemplate},
        data() {
            return {
                previewType: previewType,
                sizeOptions: [
                    {
                        value: 'size',
                        label: 'По ширине'
                    },
                    {
                        value: 'around',
                        label: 'По ширине с отступами'
                    },
                    {
                        value: 'center',
                        label: 'По центру'
                    }
                ]
            }
        }
    }
</script>

<style src="./style.css"/>