<template>
    <div id="app" class="App__content">
        <Header
                :currentStep="currentStep"
                :maxStep="maxStep"
                :cost="cost"
                @changeCount="changeCount"/>
        <div class="App__main flex">
            <SwitchableArea
                    :step="currentStep"
                    :type="previewType"
                    :settings="previewSettings"
                    @typeChange="typeChange"
            />
            <Previewer
                    :type="previewType"
                    :step="currentStep"
                    :settings="previewSettings"
                    @changeStep="changeStep"
            />
        </div>
    </div>
</template>

<script>
    import Header from "@/components/Header/Header";
    import Previewer from "@/components/Previewer/Previewer";
    import SwitchableArea from "@/components/SwitchableArea/SwitchableArea";
    import {btnType, previewType, price} from "@/constants";
    import {ObjectUtils} from "@/helpers/ObjectUtils";

    export default {
        name: 'App',
        components: {
            Header, Previewer, SwitchableArea
        },
        data() {
            return {
                currentStep: 1,
                maxStep: 8,
                cost: price.type.landing,
                previewType: previewType.landing,
                previewSettings: {
                    header: {
                        useHeader: true,
                        background: '#ceceee',
                        isPercentHeight: false,
                        height: 100,
                        hasTitle: true,
                        title: {
                            text:'Интерактивное ТЗ',
                            color: '#333333',
                            size: 1.5,
                            border: true,
                            borderColor: '#333333',
                            twoLine: true,
                            twoLineText: 'Мы создаем проекты Ваших идей',
                            twoLineColor: '#676767',
                            twoLineSize: 1,
                        },
                        align: 'size',
                        hasLogo: true,
                        logo: {
                            url: 'https://clipart-best.com/img/chrome-logo/chrome-logo-clip-art-32.png',
                            width: 200,
                            height: 100,
                            auto: true
                        },
                        hasContacts: true,
                        contacts: {
                            hasPhone: true,
                            hasEmail: true,
                            phone: '+7 (999) 999-99-99',
                            color: '#080808',
                            size: 1.1,
                            email: 'test@test.ru',
                            useIcon: true,
                            clickable: true,
                            decoration: false
                        },
                        additionalInfo: ''
                    },
                    menu: {
                        isHorizontal: true,
                        useIcon: true,
                        useText: true,
                        size: 65,
                        background: '#545c64',
                        color: '#fff',
                        activeBackground: '#ffd04b',
                        hasMinimize: false,
                        fixed: true,
                        useAbout: true,
                        useContacts: true,
                        useReviews: true,
                        useCommand: true,
                        useProject: true,
                        items: [
                            {
                                id: 1,
                                name: 'Главная',
                                icon: 'el-icon-s-home',
                                description: ''
                            }
                        ]
                    }
                }
            }
        },
        methods: {
            changeCount: function (type) {
                if (type === btnType.next) {
                    this.currentStep++;
                } else {
                    this.currentStep--;
                }
            },
            typeChange: function (type) {
                this.previewType = type;
                const key = ObjectUtils.getKeyByValue(type, previewType);

                if (key) {
                    this.cost = price.type[key];
                }
            },
            changeStep: function (step) {
                this.currentStep = step;
            }
        },
    }
</script>

<style src="./assets/styles/style.css"/>
