<template>
    <el-menu
            :default-active="activeIndex"
            class="el-menu-demo Menu__content"
            :collapse="isCollapse && settings.menu.hasMinimize && !settings.menu.isHorizontal"
            :class="{
                'Menu__fixed': settings.menu.fixed && settings.menu.isHorizontal,
            }"
            :style="{
                width: settings.menu.isHorizontal ? '100%' : !isCollapse ? (settings.menu.size + 'px') : '',
                height:  !settings.menu.isHorizontal ? '100%' : (settings.menu.size + 'px')
            }"
            :mode="settings.menu.isHorizontal ? 'horizontal' : 'vertical'"
            :background-color="settings.menu.background"
            :text-color="settings.menu.color"
            :active-text-color="settings.menu.activeBackground">
        <div class="flex flex-align-center" :class="{'flex-justify-center': isCollapse, 'flex-justify-end': !isCollapse}">
            <el-button
                    class="Menu__btnCollapse"
                    v-if="!settings.menu.isHorizontal && settings.menu.hasMinimize"
                    type="primary"
                    :icon="isCollapse ? 'el-icon-s-unfold' : 'el-icon-s-fold'"
                    circle
                    size="small"
                    @click="isCollapse = !isCollapse"
            />
        </div>
        <el-menu-item
                v-for="item in settings.menu.items"
                :key="item.id"
                :index="item.id"
        >
            <i :class="item.icon"  v-if="settings.menu.useIcon"></i>
            <template slot="title" v-if="settings.menu.useText">
                <span>{{ item.name }}</span>
            </template>
        </el-menu-item>
    </el-menu>
</template>

<script>
    export default {
        name: "Menu",
        props: {
            settings: {required: true, type: Object}
        },
        data() {
            return {
                activeIndex: 1,
                isCollapse: false
            }
        }
    }
</script>

<style src="./style.css"/>